import React,{useEffect, useRef} from 'react'
import { TweenMax, Power3 } from 'gsap'
import downArrow from '../images/down-arrow.png'
import { Link, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

function Clientel({up4,clientRef,handleScrollDown,contactF}) {

    var c1 = useRef();
    var c2 = useRef();
    var c3 = useRef();
    var c4 = useRef();
    var c5 = useRef();


    const animate=()=>{
        // alert("hello");
        TweenMax.staggerFrom([c1,c2,c3,c4,c5],1,{y:1000,ease:Power3.easeOut},.2);
        // TweenMax.from(c2,0.8,{y:1000,ease:Power3.easeOut});
    }

    useEffect(() => {
        if(up4==true)
        {
            animate();
        }
    }, [up4]);


    return (
        <div  id="cl" ref={clientRef} className="container clientDiv" >

            <h1 style={{marginBottom:"180px",fontWeight:"100"}}>Our Clientele</h1>
         
            <div class="container" >
                <div class="row">
                    <div class="col-sm">
                        <div className="ClientImg1" ref={(el)=>{c1 =el}}>
                        </div>

                    </div>
                    <div class="col-sm">
                        <div className="ClientImg2" ref={(el)=>{c2 =el}}>
                        </div>
                    </div>
                    <div class="col-sm">
                        <div className="ClientImg3" ref={(el)=>{c3 =el}}></div>
                    </div>
                    <div class="col-sm">
                        <div className="ClientImg4" ref={(el)=>{c4 =el}}></div>
                    </div>
                    <div class="col-sm">
                        <div className="ClientImg5" ref={(el)=>{c5 =el}}></div>
                    </div>
                </div>
            </div>
            <Link to="cf" smooth={true} offset={0} duration={800}  delay={200}>        
            <img src={downArrow}  
                                 className={up4?"downArrow40":"downArrow41"}
                                //  onClick={()=>handleScrollDown(contactF)}
                    />
            </Link>
        </div>
    )
}

export default Clientel