import React, { useEffect, useState, useRef } from 'react';
import Clientel from './Clientel';
import ContactForm from './ContactForm';
import Services from './Services';
import logo from "../images/Logo3.png"
import downArrow from '../images/down-arrow.png'
import { TweenMax, Power3 } from 'gsap'
import { Link, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

function Home({ }) {
    // const dispatch = useDispatch();
    // const getPics = async () => {
    //     dispatch(getPicsAction(page))
    // }
    const pointer = useRef();
    var top = useRef();
    var chef = useRef();
    var downA = useRef();
    var topH = useRef();
    var weDo = useRef();
    var serve = useRef();
    var clientRef = useRef();
    var contactF = useRef();

    const [showNav, setShowNav] = useState(false);
    const [up, setUp] = useState(false);
    const [up1, setUp1] = useState(true);
    const [up2, setUp2] = useState(false);
    const [up4, setUp4] = useState(false);
    const [up5, setUp5] = useState(false);
    const [down, setDown] = useState(true);

    const listenScroll = () => {

        let hieghtToHidden = 400;
        const winScroll = document.body.scrollTop || document.documentElement.scrollTop;

        if (hieghtToHidden <= winScroll) {
            setShowNav(true);
            setUp(true);
        }
        else {
            setShowNav(false);
            setUp(false);
        }

        window.onscroll = function (e) {
            // console.log(this.oldScroll > this.scrollY, "direction");
            if (this.oldScroll > this.scrollY) {
                setUp(true);
            }
            else {
                setUp(false);
            }

            this.oldScroll = this.scrollY;
        }

        if (winScroll >= 300 && winScroll < 1200) {
            setUp1(false);
        }
        else {
            setUp1(true);
        }

        if (winScroll >= 1200) {
            setUp2(true);
        }
        else {
            setUp2(false);
        }

        if (winScroll >= 1700) {
            setUp4(true);
        }
        else {
            setUp4(false);
        }

        if (winScroll >= 2400) {
            setUp5(true);
        }
        else {
            setUp5(false);
        }



        console.log(winScroll, "winScroll");
        pointer.current.style.top = pointer?.current?.style?.top + winScroll + "px";

    };


    const animateTopUpImg = () => {
        TweenMax.to(chef, 0.8, { x: -400, opacity: 1, ease: Power3.easeOut });
        TweenMax.to(topH, 0.8, { y: -400, opacity: 1, ease: Power3.easeOut });
        setTimeout(() => {
            TweenMax.to(downA, 12, { opacity: 0.7, ease: Power3.easeOut });
        }, [2000])
    }

    const handleScrollDown = (elementRef) => {
        window.scrollTo({
            top: elementRef.current.offsetTop,
            behavior: "smooth"
        })
    }


    useEffect(() => {


    }, [down]);


    useEffect(() => {
        window.addEventListener("scroll", listenScroll);
        animateTopUpImg();
    }, []);

    return (
        <>
            <div ref={pointer} >
                <div
                    className={showNav ? "topN2" : "topN1"}>
                    <img
                        className={showNav ? "logo1" : "logo0"}
                        src={logo}
                    >
                    </img>
                </div>


                <div class="" id="tp" ref={top} style={{ textAlign: "center", marginBottom: "100px" }}>
                    <div class="row" style={{ width: "100%" }}>
                        <div class="col-sm col-lg-4" style={{ textAlign: "center", border: "1px solid transparent" }}>
                        </div>
                        <div class="col-sm  col-lg-4" style={{ textAlign: "center", border: "1px solid transparent" }}>
                            <h1 className={"topHeading"} ref={(el) => topH = el}>We Convert Restaurant into High Revenue Generating Entities</h1>
                            <Link to="wD" smooth={true} offset={0} duration={800}  delay={200}>                              
                            <img ref={(el) => downA = el} src={downArrow}
                                className={down ? "downArrow" : "downArrow"}
                                // onClick={() => handleScrollDown(weDo)}
                            />
                            </Link>

                        </div>
                        <div class="col-sm col-lg-4" style={{ textAlign: "center", border: "1px solid transparent" }}>
                            <div ref={(el) => chef = el} className={"chefImg1"}></div>
                        </div>
                    </div>
                </div>
                <div className="weDoDiv" id="wD" ref={weDo}>
                    <h1 className={`container ${up1 ? "weDo" : "weDo1"}`} style={{ fontWeight: 100 }}>What We Do ..</h1>
                    <h1 className={up1 ? "bigHeading" : "bigHeading1"}>We Bring FootFall To Your Restaurants Like Never Before</h1>
                    <Link to="sr" smooth={true} offset={0} duration={800}  delay={200}>          
                    <img src={downArrow}
                        className={up1 ? "downArrow10" : "downArrow11"}
                        // onClick={() => handleScrollDown(serve)}
                    />
                    </Link>
                </div>
                <Services handleScrollDown2={handleScrollDown} clientRef={clientRef} serve={serve} up2={up2} />
                <Clientel handleScrollDown={handleScrollDown} clientRef={clientRef} contactF={contactF} up4={up4} />
                <ContactForm top={top} handleScrollDown={handleScrollDown} contactF={contactF} up5={up5} />
            </div>
        </>
    )
}

export default Home