import React,{useState,useRef} from 'react'
import emailjs from "@emailjs/browser"
import downArrow from '../images/upload.png'
import { Link, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll'

function ContactForm({up5,contactF,handleScrollDown,top}) {

    const form = useRef();
    const [name, setName] = useState("");
    const [company, setCompany] = useState("");
    const [email, setEmail] = useState("");
    const [number, setNumber] = useState("");
    const [country, setCountry] = useState("");
    const [service, setService] = useState("");

    const sendEmail = (e) => {
        // setThanks(true);
        e.preventDefault();
        // emailjs.sendForm('service_4iytwqd', 'template_pptnbt9', form.current, 'o24Fc3Wsy3dQZti08')
        emailjs.sendForm('', '', form.current, '')
          .then((result) => {
            // history.push('/thanks')
          }, (error) => {
            alert("please try again some error occurred");
          });
      };
    


    return (
        <div ref={contactF} id="cf">
            <section>
                <div class="formDiv container" >
                    <h1 class="formHeading" style={{marginBottom:"50px",fontWeight:"100"}}>Contact us Form</h1>
                    <form ref={form} onSubmit={sendEmail}>

                        <label class={`${up5?"l":"l1"}`} for="fname">Your Name</label>
                        <input class={`${up5?"inp":"inp1"} form-control`} type="text" name="name" value={name} onChange={(e) => { setName(e.target.value) }} />

                        <label class={`${up5?"l":"l2"}`} for="fname">Company</label>
                        <input class={`${up5?"inp":"inp2"} form-control`} type="text" name="company" value={company} onChange={(e) => { setCompany(e.target.value) }} />

                        <label class={`${up5?"l":"l3"}`} for="fname">Email Id</label>
                        <input class={`${up5?"inp":"inp3"} form-control`} type="email" name="email" value={email} onChange={(e) => { setEmail(e.target.value) }} />

                        <label class={`${up5?"l":"l4"}`} for="fname">Contact Number</label>
                        <input class={`${up5?"inp":"inp4"} form-control`} type="number" name="number" value={number} onChange={(e) => { setNumber(e.target.value) }} />

                        <label class={`${up5?"l":"l5"}`} for="fname">Country</label>
                        <input class={`${up5?"inp":"inp5"} form-control`} type="text" name="country" value={country} onChange={(e) => { setCountry(e.target.value) }} />

                        <label class={`${up5?"l":"l6"}`} for="fname">Service Required</label>
                        <input class={`${up5?"inp":"inp6"} form-control`} type="text" name="service" value={service} onChange={(e) => { setService(e.target.value) }} />

                        <input className="trendBtn" type="submit" value="Book Your Free Consultation today" />
                    </form>

                </div>
                <Link to="tp" smooth={true} offset={-80} duration={800}  delay={200}>        
                <img src={downArrow}  
                               className={up5?"downArrow50":"downArrow51"}
                                //  onClick={()=>handleScrollDown(top)}
                    />    
                </Link>
            </section>
        </div>
    )
}

export default ContactForm